
.section {
    padding-top: 60px;
    padding-bottom: 60px;

    @media screen and (max-width: 767px) {
        padding-top: 20px;
        padding-bottom: 40px;
    }
}

.collection{
 
    @media screen and (min-width: 768px){
        display: flex;
        flex-wrap: wrap;
        margin-left: -32px;
        margin-top: -32px;
    }
    @media screen and (min-width: 1024px){
       margin-left: -16px;     
    }
}

.collection >.movie-card {
   
    @media screen and (max-width: 767px) {
        &:not(:last-child) {
            margin-bottom: 20px;
        }
    }
    @media screen and (min-width: 768px){
        display: flex;
        flex-direction: column;
        flex-basis: calc(100% / 2 - 32px);
        width: 335px;
        margin-left: 32px;
        margin-top: 32px;
        
    }
    @media screen and (min-width: 1024px){
        flex-basis: calc(100% / 3 - 16px);
        width: 310px;
        margin-left: 16px;
    }
}

.movie-info {
    transform: scale(1);
    transition: transform linear 250ms;
    padding-top: 8px;
}
  
  .movie-info::after {
      content: '';
      position: absolute;
      display: block;
      height: 3px;
      width: 100%;
      border-radius: 2px;
      background-color: var(--btn-hover-background);

      transform: scale(0);
      opacity: 0;
      transition: transform linear 350ms, opacity linear 250ms;
  }

 .collection > .movie-card:hover .movie-info::after {
      transform: scale(1);
      opacity: 1;
  }

.poster-thumb {
    border-radius: 5px;
    overflow: hidden;
    flex-grow: 1;
}

.poster {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
   
}
.collection>.movie-card:hover .poster {
    transform: scale(1.1);
}
.movie-link {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.movie-genres {
    padding-left: 5px;

    &:first-child {
        padding-left: 0;
    }

    &:not(:last-child)::after {
        content: ",";
    }
}

.movie-date {
    padding-left: 5px;
    padding-right: 8px;
    border-left: 1px solid var(--secondary-text-color);
}


.movie-rating{
    background: var(--secondary-text-color);
    color: var(--btn-border-color);
    align-items: center;
    padding: 1px 6px;
    border-radius: 5px;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
}

.movie-title{
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    text-transform: uppercase;
    color: var(--main-text-color);
}



.movie-description {
  flex-grow: 1;
    display: flex;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    color: var(--secondary-text-color);
}

.genres-list {
    display: flex;
    margin-right: 5px;
}